html {
  --link-color: #00acdc;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list :not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover {
  color: var(--link-color);
}

.is-active {
  color: var(--link-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--link-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
